

.dropdown{
  display: inline-block;
  position: relative;

  .dropdownBtn{
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
    }

  .content{
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;

    div{
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
    }
    div:hover{ background-color: #ddd; }
  }
}
.dropdown:hover .content{display: block;}
.dropdown:hover .dropdownBtn{ background-color: #3e8e41;}
